<template>
  <el-table
    :data="resultList"
    style="width: 100%">
    <el-table-column
    prop="actId"
    label="Act Id"
    width ="100"
    ></el-table-column>
    <!--<el-table-column-->
      <!--prop="firstName"-->
      <!--label="First/Middle Name"-->
      <!--width="150">-->
    <!--</el-table-column>-->
    <el-table-column
      prop="name"
      label="Name"
      width="150">
    </el-table-column>

    <el-table-column
      fixed
      prop="nextAvailableDate"
      label="Next Available Date"
      width="160">
    </el-table-column>
    <el-table-column
      prop="skills"
      label="Skills"
      width="120">
    </el-table-column>
    <el-table-column
      prop="residency"
      label="Residency"
      width="120">
    </el-table-column>
    <el-table-column
      prop="citizenship"
      label="Citizenship"
      width="120">
    </el-table-column>
    <el-table-column
      prop="birthday"
      label="Date of Birth"
      width="120">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="Operations"
      width="120">
      <template slot-scope="scope">
        <el-button @click="handleClick" type="text" size="small">Detail</el-button>
        <el-button type="text" size="small">Edit</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
  export default {

    props:['resultList'],
    methods: {
      handleClick() {
        console.log('click');
      }
    },
    data() {
      return {

      }
    }
  }
</script>



